@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100;0,9..40,200;0,9..40,300;0,9..40,400;0,9..40,500;0,9..40,600;0,9..40,700;0,9..40,800;0,9..40,900;0,9..40,1000;1,9..40,100;1,9..40,200;1,9..40,300;1,9..40,400;1,9..40,500;1,9..40,600;1,9..40,700;1,9..40,800;1,9..40,900;1,9..40,1000&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'DM Sans', sans-serif;
}
.App{
  margin: 0 50px;
}
/* -------------------- navbar ------------------------ */
.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  color: #fff;
  background: transparent;
  padding-top: 20px;
  position: relative;
  z-index: 1000;
}
.logo{
   width: 40px;
   height: 40px;
   margin-right: 15px;
   background: transparent;
}
.logo_sect{
  display: flex;
  align-items: center;
  font-weight: 500;
  background: transparent;
}
.logo_sect span{
  background: transparent;
}
.signin_sect{
  display: flex;
  align-items: center;
  background: transparent;
}
.signin_sect span{
  background: transparent;
}
.menu{
  width: 30px;
  height: 30px;
  margin-left: 20px;
  background: transparent;
}
input{
  outline: none;
  border: none;
  padding: 10px;
  width: 450px;
  color: #fff;
  background: transparent;
}
input::placeholder{
  color: #fff;
}
.search_sect{
  flex-basis: 47%;
  border: 2px solid #fff;
  border-radius: 10px;
  padding: 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
}
.search{
  margin-right: 10px;
  background: transparent;
}
/* ------------------------------- header ------------------------------------ */
.banner{
  min-height: fit-content;
  width: 100vw;
  color: #fff;
  position: relative;
  padding-bottom: 300px;
  
}

.backdrop{
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  top: 0;
  right: 0;
  z-index: 10;

}
.banner_details{
  position: relative;
  z-index: 1000;
  width: 60%;
  margin: 0 50px;
  margin-top: 80px;
}
.banner_title{
  font-size: 40px;
}
.banner_rating{
  display: flex;
  align-items: center;
  gap: 80px;
  margin: 15px 0;
}
.banner_button{
  margin: 10px 0;
  padding: 12px;
  background: #be123c;
  border-radius: 6px;
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 13px;
  
}
.play{
  margin-left: 10px;
}
.err{
  color: #fff;
  font-size: 30px;
  font-weight: 800;
  background: #be123c;
  padding: 20px;
  margin: 50px;
  border-radius: 6px;
}

/* -------------------------------- Movies ----------------------------------- */
.movie_top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  margin: 20px 0;
}
.more{
  display: flex;
  align-items: center;
  gap: 10px;
}

.movies{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  justify-content: center;

}
.cardImg{
  width: 200px;
  height: 296px;
}
.card{
  width: 200px;
  height: 400px;
  justify-self: center;
  
}
.rel{
  position: relative;
}
.fav{
  position: absolute;
  right: 17px;
  top: 10px;
  border-radius: 50%;
  background: rgb(255, 255, 255, 0.3);
  padding: 5px;
  cursor: pointer;
}
.fav.active{
  background: #be123c;
}
.imdb, .strwbry{
  display: flex;
  align-items: center;
  gap: 10px;
}
.rating{
  margin-top: 2px;
  display: flex;
  justify-content: space-between;
}

.movie_name{
  font-size: 16px;
  font-weight: 800;
  height: 40px;
}
a{
  text-decoration: none ;
  color: black;
}
.release_date{
  font-size: 10px;
  margin: 5px 0;
  color: grey;
}
/* =========================== loader ===================================== */
.loader {
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top: 4px solid #3498db;
  width: 70px;
  height: 70px;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
/* ==================================== Movie Details =========================================== */
.details_page{
  display: flex;
  gap: 30px;
  max-height: 100vh;
}
.sidebar{
  display: flex;
  flex-direction: column;
  height: fit-content;
  border: 1px solid #B2B2B2;
  border-radius: 0 30px 30px 0;

}
.logo_details{
   padding: 20px;
}
.home, .movies_projector, .tv_series, .upcoming, .logout{
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 25px;
  font-size: 14px;
  padding-left: 40px;
}
.icon{
  width: 20px;
  height: 20px;
}
.sidebar_card{
  padding: 20px;
}
.movies_projector{
  background: #f8e7eb;
   border-right: 4px solid #be123c;
  
}
.card_wrapper{
  border: 1px solid #B2B2B2;
  padding: 10px;
  padding-top: 40px;
  border-radius: 10px;
  font-size: 14px;
  width: fit-content;
  color: #5b5a5a;
}
.card_top{
  font-weight: 700;
}
.card_mid{
  margin: 4px 0;
}
.card_button{
  background:  #f0c8d2;
  text-align: center;
  padding: 5px;
  border-radius: 20px;
  color:  #be123c;
  margin: 10px 0;
  width: 120px;
  margin-left: 7px;

}
.img_section{
  margin: 20px 0;
  margin-right: 20px;
  position: relative;
}
.country_backdrop{
  width: 100%;
  height: 310px;
  object-fit: fill;
  border-radius: 20px;
}
.play_icon{
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  padding: 6px;
  height: 40px;
  margin-left: 28px;
}
.trailer{
  position: absolute;
  top: 40%;
  right: 45%;
  color: #fff;
}
.details_section{
  display: flex;
  gap: 10px;
  flex-basis: 66%;
}
.movie_info{
  padding: 0 15px;
}
.title_info{
  font-weight: 600;
}
.genre_name{
  width: fit-content;
  border-radius: 20px;
  padding: 3px 10px;
  font-size: 12px;
  border: 1px solid #f8e7eb;
  color: #be123c;
  margin-right: 5px;
  margin-top: 10px;
}
.movie_overview{
  margin: 15px 0;
  max-height: 80px;
  overflow-y: scroll;
}
.movie_overview::-webkit-scrollbar {
  display: none;
}
.credits{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.nominations{
  display: flex;
  align-items: center;
  gap: 25px;
  border: 1px solid #c7c7c7;
  border-radius: 10px;
  width: 100%;
  margin-top: 20px;
}
.top_rated{
  background: #be123c;
  color: #fff;
  padding: 10px 15px;
  border-radius: 10px;
}
.awards{
  color:  #be123c;
  padding: 3px;
  word-spacing: 5px;
  position: relative;

}
.down{
  width: 20px;
  height: 20px;
  position: absolute;
  left: 255%;
}
.watch_options{
  padding-right: 30px;
  flex-basis: 33%;
}
.star_rating{
  text-align: right;
  color: #666666;
}
.greyed{
  color: #e8e8e8;
}
.star_icon{
  height: 20px;
  width: 20px;
  margin-right: 10px;
  vertical-align: top;
}
.showtimes{
  width: 100%;
  background: #be123c;
  color: #fff;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 7px;
  padding: 8px;
  font-size: 12px;
  margin-top: 10px;
}
.ticket{
  height: 20px;
  width: 20px;
}
.watch{
  width: 100%;
  background: #f8e7eb;
  color: #333333;
  border-radius: 6px;
  border: 1px solid #be123c;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 7px;
  padding: 8px;
  font-size: 12px;
  margin-top: 10px;
}
.List{
  height: 20px;
  width: 20px;
}

.rect{
  height: 180px;
  width: fit-content;
  margin-top: 10px;
}
.best{
  position: relative;
  height: 180px;
  width: fit-content;
}
.overlay{
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 10px 15px;
  font-size: 12px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  bottom: -9px;
  color: #fff;
}
.whitelist{
  height: 15px;
  width: 15px;
}

/* ================================ ERROR PAGE ================================================== */
.error{
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 30px;
  color: #fff;
  background: linear-gradient(65deg, rgb(105, 229, 167), rgb(26, 97, 169), rgb(192, 58, 58));

}
.home_button{
  color: #fff;
  padding: 10px;
  margin-left: 10px;
  background: #be123c;
  border-radius: 6px;
  width: fit-content;
  
}
.movies_error{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #3498db;
  height: 100vh;
}
.err-content{
  padding: 50px;
  margin: 20px;
  color: #fff;
  width: fit-content;
  height: fit-content;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(3px);
  border-radius: 20px 20px 20px 0;
  line-height: 40px;

}
.err_btn{
  background: #be123c;
  padding: 12px;
  border-radius:  20px 20px 20px 0;
  color:aliceblue;
  margin-top: 100px;
}

/* ========================== footer ===========================*/
.footer{
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin-top: 30px;
  font-weight: 800;
}
.socials{
  display: flex;
  gap: 40px;
  justify-content: center;
  align-items: center;
}
.socials>img{
  cursor: pointer;
}
.footer_details{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.acknowledgements{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
  color:#666666;
}
/* ======================== responsiveness ================================ */
@media (min-width: 700px) and ( max-width: 1000px) {
  .movies{
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    justify-content: center;
  
  }
  input{
    width: 250px;
    font-size: 15px;
  }
  .navbar{
    font: 15px;
  }
  .details_page {
    font-size: 12px;
    gap: 20px;
  }
  .home, .movies_projector, .tv_series, .upcoming, .logout{
    padding-left: 20px;
    padding: 10px;
    font-size: 12px;
  }
  .movie_overview{
    margin: 15px 0;
    max-height: 60px;
    overflow-y: scroll;
  }
  .title_info{
    line-height: 25px;
  }
  .movie-overview{
    margin-top: 0;
  }
  .credits{
    gap: 15px;
  }
  /* .details_page{
    flex-direction: column;
    gap: 2px;
  }
  .details_section{
    flex-direction: column;
    align-items: center;
    justify-content: center;
  } */
  
}

@media (min-width: 460px) and ( max-width: 700px) {
  .movies{
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    justify-content: center;
  
  }
  .banner_details{
    width: 70%;
  }
  .navbar{
    display: block;
    font-size: 13px;
  }
  .signin_sect{
    position: absolute;
    top: 25px;
    right: 0%;
  }
  .logo_sect{
    margin-bottom: 30px;
  }
  .details_section{
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
 
}
@media (min-width: 10px) and ( max-width: 460px){
  .App{
    margin: 0 20px;
  }
  .movies{
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    justify-content: center;
  }
  .navbar{
    display: block;
    font-size: 10px;
    margin: 0 20px;
  }
  .banner_details{
    position: relative;
    z-index: 1000;
    width: 70%;
    margin: 0 20px;
    margin-top: 50px;
    font-size: 12px;
  }
  .signin_sect{
    position: absolute;
    top: 25px;
    right: 0%;
  }
  .logo_sect{
    margin-bottom: 30px;
  }
  .footer{
    display: flex;
    flex-direction: column;
    font-size: 10px;
    gap: 30px;
    margin-top: 30px;
    font-weight: 800;
  }
  .socials>img{
    height: 15px;
    width: 15px;
  }
  .footer_details{
    gap: 12px;
  }
  .socials{
    gap: 12px;
  }
  .banner{
    background-size :auto;
    background-position: center;
  }
  /* .cardImg{
    width: 250px;
    height: 370px;
  }
  .card{
    width: 250px;
    height: 450px;
    justify-self: center;
  } */
}
@media ( max-width: 1000px) {
  .details_page{
    flex-direction: column;
    gap: 2px;
  }
  .home, .movies_projector, .tv_series, .upcoming, .logout{
    padding: 10px;
  }
  .home>span, .movies_projector>span, .tv_series>span, .upcoming>span, .logout>span{
    display: none;
  }
  .logo_details{
    padding: 10px;
    margin-bottom: 0px;
  }
  
  .sidebar{
    flex-direction: row;
    border-radius: 0 0 30px 30px;
    justify-content: space-around;
    align-items: center;
    height: 46px;
    width: 100vw;
    font-size: 10px;
  }
  .sidebar_card{
    display: none;
  }
  .movies_projector{
    background: #f8e7eb;
    border-right: none;
    border-bottom: 4px solid #be123c;
    
  }
  .logo_details>img{
    height: 25px;
    width: 25px;
  }
  .img_section{
    margin: 10px 20px;
    position: relative;
  }
  .details_page {
    font-size: 12px;
  }
  .details_section{
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
}
@media ( max-width: 553px){
/* .watch_options{
  display: none;
} */
.details_section{
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.star_rating{
  text-align: center;
  color: #666666;
}
/* .showtimes, .watch{
  width: 70%;
} */
.icon{
  width: 12px;
  height: 12px;
}
.sidebar{
  height: 38px;
}
.down{
  display: none;
}
.details_page {
  font-size: 12px;
}
.featured{
  font-size: 15px;
}
}